import React from 'react';
import MaterialTable from 'material-table';
import {connect} from "react-redux";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../../Images/IconPack";

import InputLabel from "@material-ui/core/InputLabel/InputLabel";

import TextField from "@material-ui/core/TextField/TextField";
import { FormattedMessage } from 'react-intl';

import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';


const CorporateCost = (props) => {
    const [searchValue, setSearchValue] = React.useState('');
    const [dateReverse, setDateReverse] = React.useState(false);



    const getDayRow = (type) => {
        let date = new Date();
        var month = ["Jan", "Feb", "Mar", "Apr", "May", "Jun",
            "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"][date.getMonth()];
        let day;
        switch (type) {
            case 'Today':
                day = date.getDate();
                break;
            case 'Yesterday':
                day = date.getDate() - 1;
                break;
            default:
                break;
        }

        return (<div className="day-row">
            <b className="color-white">{type}</b>
            <span className="color-grey inline">{day} {month}</span>
        </div>);
    }


    const getTbodyData = () => {

            return (<>
                {getDayRow('Today')}
                <div className="tabsContentHeader mb-10">Amount / User</div>

                {props.costsData.map((i, key) => {
                    return itemHTML(i, key);
                })}

                {getDayRow('Yesterday')}

                {props.costsData.map((i, key) => {
                    return itemHTML(i, key);
                })}

            </>);

    };




    const prepearAmount = (amount, type) => {
        switch (type) {
            case 'loss':
                return (
                    <span className="red">{amount}</span>)
                break;
            case 'profit':
                return (<span className="green">{amount}</span>)
                break;
            default:
                return amount
                break;
        }
    }


    const itemHTML = (i, key) => {
        return  (
            <Accordion >
                <AccordionSummary
                    aria-controls={"panel"+key+"a-content"}
                    id={"panel"+key+"a-header"}
                >
                    <span className="color-red mr-10 bold">${i.amount}</span><span className="bold icon-inside align-items-baseline inline">{IconPack.userIcons.forTable} {i.user}</span>
                </AccordionSummary>
                <AccordionDetails>
                    <span className="accordion-data-description">Cost action</span>
                    {i.costAction}

                    <span className="accordion-data-description">Description</span>
                    {i.description}

                    <span className="accordion-data-description">Cost detail</span>
                    {i.costDetail[0]}
                    <span className="ml-10 icon-inside inline align-items-baseline">{IconPack.licenseIcons.cog} {i.costDetail[1]}</span>

                    <span className="accordion-data-description">Bought left</span>
                    {i.boughtLeft}

                    <span className="accordion-data-description">Date</span>
                    13 Feb, 11:10:22
                </AccordionDetails>
            </Accordion>

        );
    }




    let searchedData = props.costsData.filter(i => {

        if (i.user.toLowerCase().includes(searchValue.toLowerCase()) ||
            i.costAction.toLowerCase().includes(searchValue.toLowerCase()) ||
            i.costDetail[0].toLowerCase().includes(searchValue.toLowerCase()) ||
            i.costDetail[1].toLowerCase().includes(searchValue.toLowerCase()) ||
            i.boughtLeft.toLowerCase().includes(searchValue.toLowerCase())
          ) {
            return i
        }

    })

    searchedData = searchedData.map((i, key) => {

        return itemHTML(i, key);
    });


    /* active */
    return (
        <div className="ÑustomStyle">
            <div className="filterPanel filterPanel--mobile ">

                <FormattedMessage  id='filterPanel.search'>
                    {(msg) => ( <TextField
                        variant="filled"
                        fullWidth
                        className="search_area"
                        placeholder={msg}
                        value={searchValue}
                        onChange={(e)=>{setSearchValue(e.target.value)}}
                    >
                    </TextField>)}
                </FormattedMessage>

                <div className="search_additional_components">
                    <div className="calendar">{IconPack.searchComponents.calendar}</div>
                </div>
            </div>

              {searchValue.length ? searchedData : getTbodyData() }

        </div>
    );
}

const mapStateToProps = (state) => ({
    costsData: state.corporateLicenseCosts.costs,
});


export default connect(mapStateToProps)(CorporateCost);